<template>
  <v-container fluid>
    <v-card>
      <v-container>
        <v-row>
          <v-col cols="4">
            <v-dialog
              ref="dialog"
              v-model="datePickerDialog"
              :return-value.sync="dates"
              persistent
              width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="dates"
                  :label="$t('日期筛选')"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                />
              </template>
              <v-date-picker
                v-model="dates"
                range
                scrollable
              >
                <v-spacer/>
                <v-btn
                  text
                  color="primary"
                  @click="datePickerDialog = false"
                >
                  Cancel
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="$refs.dialog.save(dates)"
                >
                  OK
                </v-btn>
              </v-date-picker>
            </v-dialog>
          </v-col>
          <v-col cols="4">
            <v-dialog
              ref="timeDialog"
              v-model="timeStartPickerDialog"
              :return-value.sync="hourStart"
              persistent
              width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="hourStart"
                  :label="$t('startTime')"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                />
              </template>
              <v-time-picker
                v-model="hourStart"
                range
                scrollable
              >
                <v-spacer/>
                <v-btn
                  text
                  color="primary"
                  @click="timeStartPickerDialog = false"
                >
                  Cancel
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="$refs.timeDialog.save(hourStart)"
                >
                  OK
                </v-btn>
              </v-time-picker>
            </v-dialog>
          </v-col>
          <v-col cols="4">
            <v-dialog
              ref="timeEndDialog"
              v-model="timeEndPickerDialog"
              :return-value.sync="hourEnd"
              persistent
              width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="hourEnd"
                  :label="$t('endTime')"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                />
              </template>
              <v-time-picker
                v-model="hourEnd"
                range
                scrollable
              >
                <v-spacer/>
                <v-btn
                  text
                  color="primary"
                  @click="timeEndPickerDialog = false"
                >
                  Cancel
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="$refs.timeEndDialog.save(hourEnd)"
                >
                  OK
                </v-btn>
              </v-time-picker>
            </v-dialog>
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="fixedHideAttributes"
              :label="$t('永久隐藏属性')"
              item-text="name"
              item-value="id"
              multiple
              clearable
              :items="realAttributes"
              @change="selectAttributes(fixedHideAttributes)"
              @click:clear="clearFixedHideAttribute"
            />
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="hideAttributes"
              :label="$t('隐藏属性')"
              item-text="name"
              item-value="id"
              multiple
              clearable
              :items="realAttributes"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="4">
            <v-select
              v-model="fixedHideDishes"
              :label="$t('永久隐藏菜品')"
              item-text="dishName"
              item-value="dishName"
              multiple
              clearable
              :items="allFixedDishesData"
              @change="selectFixedDishes(fixedHideDishes)"
              @click:clear="clearFixedHideDishes"
            />
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="hideDishes"
              :label="$t('选择菜品(不选即为选择所有菜品)')"
              item-text="dishName"
              item-value="dishName"
              multiple
              :items="allDishData"
              @change="selectDishes(hideDishes)"
            />
          </v-col>
          <v-col cols="4">
            <v-btn
              :disabled="tableData.length<2"
              x-large
              color="primary"
              block
              @click="showSalesTable=true"
            >
              {{ $t('查看分析报告') }}
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
    <v-dialog
      v-model="showSalesTable"
      fullscreen
      scrollable
    >
      <v-card>
        <v-toolbar
          dense
          dark
        >
          <v-toolbar-title>
            {{ $t('销售统计') }}
          </v-toolbar-title>
          <v-spacer/>
          <v-btn
            class="mr-4"
            @click="print"
          >
            <v-icon left>
              mdi-printer
            </v-icon>
            {{ $t('打印统计单') }}
          </v-btn>
          <v-icon @click="showSalesTable=!showSalesTable">
            mdi-close
          </v-icon>
        </v-toolbar>
        <v-card-text>
          <v-data-table
            fixed-header
            dense
            hide-default-footer
            disable-pagination
            height="calc(100vh - 72px)"
            :item-class="computeClass"
            :headers="headers"
            :items="tableData"
            :items-per-page="100"
          />
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
  import { PrinterSalesInSightBon, salesInsight } from '../../components/Utlis/api'
  import { StandardDishesListFactory } from 'aaden-base-model/lib/Models/AadenBase'
  import Attribute from '../../model/Attribute'
  import AttributeGroup from '../../model/AttributeGroup'
  import Dish from '../../model/Dish'
  import { IKUtils } from 'innerken-utils'
  import { formatDate } from '../../common/common'
  import Utils from '../../components/Utlis/Utils'

  export default {
    name: 'SalesInsight',
    data: function () {
      return {
        datePickerDialog: null,
        timeStartPickerDialog: null,
        timeEndPickerDialog: null,
        hourStart: '',
        hourEnd: '',
        dates: [],
        showSalesTable: null,
        unfilteredData: [],
        displayData: [],
        attributeData: [],
        hideDishes: [],
        filterDishesData: [],
        hideAttributes: [],
        fixedHideAttributes: [],
        fixedHideDishes: [],
        mergedGroup: [],
        allDishData: [],
        allFixedDishesData: [],
        fixedHeader: [
          {
            text: 'name',
            value: 'name',
          },
          {
            text: 'price',
            value: 'price',
          },
          {
            text: 'amount',
            value: 'amount',
          },
        ],
      }
    },
    computed: {
      headers () {
        return this.fixedHeader.concat(this.attributeHeader)
      },
      realAttributes () {
        return this.attributeData.reduce((arr, a) => {
          a.childAttributes.forEach(c => arr.push(c))
          return arr
        }, [])
      },
      attributeHeader () {
        return this.realAttributes
          .filter(i => !(this.hideAttributes.includes(i.id) || this.fixedHideAttributes.includes(i.id)))
          .map(a => {
            a.text = a.name
            a.value = 'attr' + a.id
            return a
          })
      },
      tableData () {
        const tableData = []
        const formatDish = (d) => {
          d.isDish = true
          d.price = `${d.originPrice} (avg:€${(d.totalPrice / d.totalCount).toFixed(2)})`
          d.amount = d.totalCount
          d.groupData.forEach(g => {
            d['attr' + g.id] = g.count
          })
          return d
        }
        const formatVariant = (v, i) => {
          v.name = 'v:' + i
          v.amount = v.count
          v.groupData.forEach(g => {
            v['attr' + g.id] = g.count
          })
          return v
        }
        if (this.hideDishes.length > 0) {
          this.filterDishesData.forEach(d => {
            tableData.push(d)
            d.variants.forEach((v, i) => {
              tableData.push(formatVariant(v, i))
            })
          })
        } else {
          this.displayData.forEach(d => {
            tableData.push(formatDish(d))
            d.variants.forEach((v, i) => {
              tableData.push(formatVariant(v, i))
            })
          })
        }
        return tableData
      },
    },
    watch: {
      dates (val) {
        this.loadSalesData(val)
      },
      hourStart (val) {
        this.loadSalesData(this.dates, val, this.hourEnd)
      },
      hourEnd (val) {
        this.loadSalesData(this.dates, this.hourStart, val)
      },
    },
    async mounted () {
      await this.loadData()
    },
    methods: {
      clearFixedHideAttribute () {
        localStorage.removeItem('fixedHideAttribute')
      },
      clearFixedHideDishes () {
        localStorage.removeItem('fixedHideDishes')
      },
      selectDishes (hideDishes) {
        this.filterDishesData = this.displayData.filter(item => (hideDishes.includes(item.name)))
      },
      selectFixedDishes (fixedHideDishes) {
        const fixedHideDishGroups = JSON.stringify(fixedHideDishes)
        localStorage.setItem('fixedHideDishes', fixedHideDishGroups)
        this.allDishData = this.allFixedDishesData.filter(item => !(fixedHideDishes.includes(item.dishName)))
        this.displayData = this.unfilteredData.filter(item => !(fixedHideDishes.includes(item.name)))
      },
      selectAttributes (attributeId) {
        const attributeIds = JSON.stringify(attributeId)
        localStorage.setItem('fixedHideAttribute', attributeIds)
      },
      computeClass (item) {
        return item.isDish ? 'dishRow normal' : 'normal'
      },
      groupElement (childList, childMatchKey) {
        const on = function (parentList, matchKey) {
          const _parentList = IKUtils.deepCopy(parentList)
          const saveIn = function (resultKey) {
            childList.forEach(c => {
              const target = _parentList.find(p => c[childMatchKey] === p[matchKey])
              if (target) {
                if (!target[resultKey]) {
                  target[resultKey] = []
                }
                target[resultKey].push(c)
              }
            })
            return _parentList
          }
          return { saveIn }
        }
        return { on }
      },
      async getAllAttribute () {
        return (await Attribute.getList())
          .filter(a => a.optattributeGroupId.length > 0)
      },
      async getAttributeGrouped () {
        const attributeGroup = await AttributeGroup.getList()
        const attribute = await this.getAllAttribute()
        return this.groupElement(attribute, 'attributeGroupId')
          .on(attributeGroup, 'id').saveIn('childAttributes')
      },
      async getSalesInfo (timeSpan, hourStart, hourEnd) {
        const data = await salesInsight(timeSpan, hourStart, hourEnd)
        if (data.length === 0) {
          return []
        }
        const dishList = StandardDishesListFactory()
          .formatList((data).content)
        const displayList = dishList.reduce((obj, i) => {
          let dict = obj[i.code]
          if (!dict) {
            obj[i.code] = {
              categoryId: parseInt(i.categoryId),
              categoryTypeId: parseInt(i.categoryTypeId),
              code: i.code,
              // name: i.code + '/' + i.name,
              name: i.name,
              totalCount: i.count,
              originPrice: i.originPrice,
              totalPrice: i.realPrice * i.count,
              variants: [],
            }
            dict = obj[i.code]
          } else {
            dict.totalCount += i.count
            dict.totalPrice += i.realPrice * i.count
          }
          const variant = dict.variants.find(v => v.identity === i.identity)
          if (variant) {
            variant.count += i.count
          } else {
            dict.variants.push({
              identity: i.identity,
              count: parseInt(i.count),
              displayApply: i.displayApply,
              price: i.realPrice,
            })
          }
          return obj
        }, {})
        return Object.values(displayList).map(u => {
          u.group = this.groupAttribute(u)
          return u
        })
      },
      groupAttribute (dish) {
        // group key
        dish.groupData = IKUtils.deepCopy(this.realAttributes)
        dish.variants.forEach(v => {
          v.groupData = IKUtils.deepCopy(this.realAttributes)
          v.displayApply.forEach(displayApply => {
            const child = dish.groupData.find(x => x.id === parseInt(displayApply.aId))
            const selfChild = v.groupData.find(x => x.id === parseInt(displayApply.aId))
            if (!child || !selfChild) {
              return
            }
            if (!selfChild.count) {
              selfChild.count = 0
            }
            selfChild.count++
            if (!child.count) {
              child.count = 0
            }
            child.count++
          })
        })
        return dish.variants.reduce((arr, v) => {
          v.displayApply.forEach(displayApply => {
            let group = arr.find(a => a.agId === displayApply.agId)
            if (!group) {
              group = {
                ...displayApply,
                values: [],
              }
              arr.push(group)
            }
            const child = group.values.find(x => x.aId === displayApply.aId)
            if (child) {
              child.count++
            } else {
              group.values.push({
                ...displayApply,
                count: 1,
              })
            }
          })
          return arr
        }, [])
      },
      async loadSalesData (timeSpan, hourStart, hourEnd) {
        const salesData = await this.getSalesInfo(timeSpan, hourStart, hourEnd)
        const totalData = salesData.reduce((obj, i) => {
          obj.totalPrice += i.totalPrice
          obj.groupData.forEach((g, t) => {
            if (!g.count) {
              g.count = 0
            }
            g.count += (i.groupData[t].count ?? 0)
          })
          obj.totalCount += i.totalCount
          return obj
        }, {
          code: '-',
          name: 'Alle',
          totalPrice: 0,
          originPrice: 0,
          totalCount: 0,
          groupData: IKUtils.deepCopy(this.realAttributes),
          variants: [],
        })
        this.unfilteredData = [totalData].concat(salesData)
        this.displayData = this.unfilteredData.filter(item => !(this.fixedHideDishes.includes(item.name)))
      },
      async loadData () {
        this.dates = [formatDate(0), formatDate(0)]
        this.attributeData = await this.getAttributeGrouped()
        this.allFixedDishesData = await Dish.getList()
        this.hourStart = this.hourStart === '' ? '00:00:00' : this.hourStart
        this.hourEnd = this.hourEnd === '' ? '23:59:59' : this.hourEnd
        await this.loadSalesData(this.dates, this.hourStart, this.hourEnd)
        if (localStorage.getItem('fixedHideAttribute')) {
          this.fixedHideAttributes = JSON.parse(localStorage.getItem('fixedHideAttribute'))
        }
        if (localStorage.getItem('fixedHideDishes')) {
          this.fixedHideDishes = JSON.parse(localStorage.getItem('fixedHideDishes'))
        }
        if (this.fixedHideDishes.length > 0) {
          this.allDishData = this.allFixedDishesData.filter(item => !(this.fixedHideDishes.includes(item.dishName)))
        } else {
          this.allDishData = IKUtils.deepCopy(this.allFixedDishesData)
        }
      },
      print () {
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        const timeSpan = [this.dates[0] + ' ' + this.hourStart, this.dates[1] + ' ' + this.hourEnd]
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie die Statistike Bon drucken?', () => {
          PrinterSalesInSightBon(timeSpan).then(() => {
            Utils.toast('Erfolgreich drucken!')
          })
        })
      },
    },
  }
</script>

